import React, { Component } from 'react';
import Layout from '../src/containers/Layout/Layout';
import BurgerBuilder from './containers/BurgerBuilder/BurgerBuilder';
class App extends Component {
  render() {
    return (
      <div >
        <Layout>
          <BurgerBuilder></BurgerBuilder>
        </Layout>
  
      </div>
    );
  }
}

export default App;



